.navbar {
  width: calc(100vw - 40px);
  height: 70px;
  bottom: 20px;
  left: 20px;
  border-radius: 20px;
  position: fixed;
  background-color: #fff;
  box-shadow: 0 4px 14px 0 rgba(55, 57, 73, 0.27);
  display: flex;
  align-items: center;
  justify-content: space-around;
  z-index: 100;
}

.nav-icon-div {
  padding: 4px;
  border-radius: 5000px;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: ease-in-out 200ms all;
}

.nav-icon-div.active {
  background-color: #002fff;
}

.nav-icon {
  width: 32px;
  height: 32px;
}
.nav-icon-div.active .nav-icon {
  filter: invert(1);
}

.sec-nav{
  max-width: calc(100vw - 80px);
  height: 70px;
  bottom: 20px;
  padding: 0 10px;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 15px;
  position: fixed;
  background-color: #fff;
  display: flex;
  align-items: start;
  justify-content: space-between;
  gap: 0px;
  z-index: 99;
  transition: ease-in-out 200ms all;
}

.sec-nav-item{
  padding: 16px;
  display: flex;
  align-items: start;
  justify-content: center;
  font-size: 14px;
  font-weight: 500;
  color: #000;
  text-decoration: none;
  transition: ease-in-out 200ms all;

}

.sec-nav-item.active{
  color: #002fff;
  /* color: #4ba2ff; */
  /* font-size: larger; */
}

.sec-nav-up{
  bottom: 65px;
  box-shadow: 0 4px 14px 0 rgba(55, 57, 73, 0.27);
  
}


.income {
  color: #00b84a;
}

.expense {
  color: #fc2222;
}